import './style.css';
import '../../global.css'
import Button from '@mui/material/Button'
import Avatar from '@mui/material/Avatar'
import { EOSBaseURL, backendBaseUrl, client_id } from '../../constants'
import { parseJSONToUrl } from '../../util/string'
import { secureTokenCookieOptions } from '../../util/cookieProvider'
import { useCookies } from 'react-cookie';
import { useAuth } from '../../util/authProvider';
import axios from 'axios';
import EpicLogo from '../../media/epic_logo.png'
import { Typography, Grid } from '@mui/material';
import DarkModeIcon from '@mui/icons-material/DarkMode';
function Login() {
  const { login } = useAuth();
  const [cookies, setCookie] = useCookies(['authtoken']);
  const handleEOSConnect = () => {
    const config = {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      }
    }
    const data = {
      scope: 'basic_profile',
      client_id,
      response_type: 'code',
      redirect_uri: window.location.origin,
    }
    const url = `${EOSBaseURL}/authorize?${parseJSONToUrl(data)}`
    const easWindow = window.open(url, "Login with Epic Online Services", "resizable=yes,width=800,height=1200");

    window.easCallback = (code) => {
      console.log(`Received Auth Code: ${code}`)
      easWindow.close()
      axios.get(`${backendBaseUrl}/exchangeauthcode/${code}`)
        .then(response => response.data)
        .then(data => {
          console.log("Storing Token:", data)
          const { expires_in, account_id } = data
          const now = new Date();
          const expires_at = new Date(now.getTime() + expires_in * 1000);
          const toStore = { ...data, expires_at }
          setCookie('authtoken', toStore, secureTokenCookieOptions)
          login(account_id)
        })
        .catch(err => {
          console.error(err)
        })
    }
  }
  return (
    <div className='centred-container'>

      <Typography>
        <Grid container direction="row" alignItems="center" sx={{ color: 'white', padding:2, gap:1 }}>
           <Typography variant="h5" color="white">
            Oracle
          </Typography><DarkModeIcon />
        </Grid>
      </Typography>
      <form className='form'>
        <Typography variant="h6" color="white">
          Sign-In
        </Typography>
        <Button
          variant="contained"
          onClick={handleEOSConnect}
          sx={{ color: 'white', backgroundColor: 'black' }}
          startIcon={<Avatar src={EpicLogo} sx={{ borderRadius: 0 }} />}
        >
          Sign in with Epic Games
        </Button>
      </form>
    </div>
  );
}


export default Login;
