import {Routes, Route} from 'react-router-dom'
import {Home, Login} from './pages'
import {ProtectedRoute} from './util/protectedRoute'
import {AuthProvider} from './util/authProvider'
import { CookiesProvider } from 'react-cookie';

/*
Make / the users profile. In the future, we'll add a frontend that doesn't require auth.
*/ 
function App() {
  return (
    <CookiesProvider>
      <AuthProvider>
        <Routes>
          <Route path="/" element={
            <ProtectedRoute>
              <Home />
            </ProtectedRoute>
          }/>
          <Route path="/login" element={<Login />}/>
        </Routes>
      </AuthProvider>
    </CookiesProvider>
  );
}

export default App;
